const defaultNamespace = '4198df1b-c8ac-44a3-9240-c76de38f80b2';

JSON.parseS = function(string) {
	let json = null;
	try {
		json = JSON.parse(string);
	} catch (e) {
		json = null;
	}
	return json;
};

JSON.copy = function(json) {
	const string = JSON.stringify(json);
	return JSON.parseS(string);
};

let pageSiteInfo = null;

class Page extends ZeroFrame {
	sendMessage(conversationId, messageBody) {
		if (conversationId === '#Lobby') {
			this.cmd('peerBroadcast', {
				message: {
					conversationId,
					body: messageBody,
					dateAdded: moment().format('x')
				},
				privatekey: false
			});
		} else {
			const group = UUID(conversationId, defaultNamespace);
			const bR = btoa(group);

			this.cmd(
				'aesEncrypt',
				[
					JSON.stringify({
						conversationId,
						body: messageBody,
						dateAdded: moment().format('x')
					}),
					bR
				],
				res => {
					// this.log('aesEncrypt', res);

					const iv = res[1];
					const encMsg = res[2];

					this.cmd('peerBroadcast', {
						message: {
							iv,
							encMsg
						},
						privatekey: false
					});
				}
			);
		}
	}

	setSiteInfo(siteInfo) {
		if (siteInfo && typeof siteInfo === 'object') {
			this.log('setting siteInfo', siteInfo);
			pageSiteInfo = siteInfo;
			let out = document.getElementById('out');

			let nUser = null;
			if (pageSiteInfo) {
				if (pageSiteInfo.hasOwnProperty('auth_address')) {
					if (pageSiteInfo.auth_address) {
						nUser = {
							userId: pageSiteInfo.auth_address,
							userName: pageSiteInfo.cert_user_id
						};
					}
				}
			}
			if (nUser) {
				window.VueApp.$store.commit(
					'ConversationProviders/provider_' + state.name + '/logIn',
					nUser
				);
			} else {
				window.VueApp.$store.commit(
					'ConversationProviders/provider_' + state.name + '/logOut'
				);
			}

			if (out) {
				out.innerHTML =
					'Page address: ' +
					siteInfo.address +
					'<br>- Peers: ' +
					siteInfo.peers +
					'<br>- Size: ' +
					siteInfo.settings.size +
					'<br>- Modified: ' +
					new Date(siteInfo.content.modified * 1000);
			}
		}
	}

	onOpenWebsocket() {
		this.cmd('siteInfo', [], function(siteInfo) {
			page.setSiteInfo(siteInfo);
		});
	}

	onRequest(cmd, message) {
		if (cmd === 'setSiteInfo') {
			this.setSiteInfo(message.params);
		} else if (cmd === 'peerReceive') {
			// and it's from PeerMessage plugin
			this.log('onRequest::peerReceive', message);

			if (!message.params.message.encMsg) {
				const msgObj = message.params.message;

				const conversationId = msgObj.conversationId;
				const messageBody = decodeURIComponent(msgObj.body);
				const dateAdded = msgObj.dateAdded;
				const identityAddress = message.params.signed_by;
				const userName = message.params.cert;
				const msgUUID = UUID(
					dateAdded,
					UUID(
						identityAddress,
						UUID(conversationId, defaultNamespace)
					)
				);

				window.VueApp.$store.dispatch(
					'ConversationProviders/provider_' +
						state.name +
						'/addMessage',
					[
						conversationId,
						identityAddress,
						userName,
						msgUUID,
						dateAdded,
						messageBody
					]
				);
			} else {
				for (const rGroup in state.conversations) {
					const group = UUID(rGroup, defaultNamespace);
					const bR = btoa(group);

					this.cmd(
						'aesDecrypt',
						[
							message.params.message.iv,
							message.params.message.encMsg,
							bR
						],
						res => {
							// this.log('aesDecrypt', res);

							if (res) {
								const msgObj = JSON.parseS(res);

								if (msgObj) {
									const conversationId =
										msgObj.conversationId;
									const messageBody = decodeURIComponent(
										msgObj.body
									);
									const dateAdded = msgObj.dateAdded;
									const identityAddress =
										message.params.signed_by;
									const userName = message.params.cert;
									const msgUUID = UUID(
										dateAdded,
										UUID(
											identityAddress,
											UUID(
												conversationId,
												defaultNamespace
											)
										)
									);

									window.VueApp.$store.dispatch(
										'ConversationProviders/provider_' +
											state.name +
											'/addMessage',
										[
											conversationId,
											identityAddress,
											userName,
											msgUUID,
											dateAdded,
											messageBody
										]
									);
								}
							}
						}
					);
				}
			}

			this.cmd('peerValid', [message.params.hash]); // This message is correct - broadcast to other peers
		} else {
			this.log('Unknown incoming message:', cmd);
		}
	}
}

