import ZeroFrame from './ZeroFrame';
import UUID from 'uuid/v4';

const debug = process.env.NODE_ENV !== 'production';

var filesadded = [];

var prodConf = null;

try {
	prodConf = require('../prod.config.js');
} catch (err) {
	prodConf = require('../prod.default.config.js');
}

const pageHidden = () => {
	let pageHiddenProp = null;

	if (typeof document.hidden !== 'undefined') {
		pageHiddenProp = 'hidden';
	} else if (typeof document.msHidden !== 'undefined') {
		pageHiddenProp = 'msHidden';
	} else if (typeof document.webkitHidden !== 'undefined') {
		pageHiddenProp = 'webkitHidden';
	}

	if (!pageHiddenProp) {
		console.warn(
			"A feature which depends on the Page Visibility API can not be used due to the lack of it's support in this browser."
		);
	}

	return pageHiddenProp !== null && document[pageHiddenProp];
};

function loadjscssfile(filename, filetype, cb) {
	let fileref;
	if (filetype === 'js') {
		fileref = document.createElement('script');
		fileref.setAttribute('type', 'text/javascript');
		fileref.setAttribute('src', filename);
	} else if (filetype === 'css') {
		fileref = document.createElement('link');
		fileref.setAttribute('rel', 'stylesheet');
		fileref.setAttribute('type', 'text/css');
		fileref.setAttribute('href', filename);
	}
	if (typeof fileref !== 'undefined') {
		fileref.onload = function(e) {
			typeof cb === 'function' && cb(undefined, filename, filetype);
		};
		fileref.onerror = function(e) {
			typeof cb === 'function' &&
				cb(
					new Error('couldn"t load file', filename, filetype),
					filename,
					filetype
				);
		};
		document.getElementsByTagName('head')[0].appendChild(fileref);
	}
}

function checkloadjscssfile(filename, filetype, cb) {
	if (filesadded.indexOf(filename) === -1) {
		loadjscssfile(filename, filetype, cb);
		filesadded.push(filename);
	} else {
		typeof cb === 'function' && cb(null, filename, filetype);
	}
}

class Page extends ZeroFrame {
	setSiteInfo(siteInfo) {
		if (siteInfo && typeof siteInfo === 'object') {
			console.log('setting siteInfo', siteInfo);
		}
	}

	onOpenWebsocket() {
		this.cmd('siteInfo', [], siteInfo => {
			this.setSiteInfo(siteInfo);
		});
	}

	onRequest(cmd, message) {
		if (cmd === 'setSiteInfo') {
			this.setSiteInfo(message.params);
		} else {
			this.log('Unknown incoming message:', cmd);
		}
	}
}
const page = new Page();

const initWorkerPromiseCB = (initWorkerPromise, pluginName, worker) => {
	initWorkerPromise(pluginName, worker).then(promise => {
		promise.removeOn('displayNotification');
		promise.addOn('displayNotification', notification => {
			if (
				!notification.title ||
				(!notification.showAlways ? !pageHidden() : false)
			)
				return null;

			const notificationId = UUID();
			page.cmd('wrapperWebNotification', [
				notification.title,
				notificationId,
				{
					...notification.options,
					focus_tab: true
				}
			]);
		});
	});
};

export const installPlugin = (pluginName, initWorkerPromise, store) => {
	if (!debug) {
		page.cmd(
			'fileGet',
			['plugins/' + pluginName + '/index.js', 'text'],
			f => {
				const url = URL.createObjectURL(
					new Blob([f], {
						type: 'text/javascript'
					})
				);
				const worker = new Worker(url);

				initWorkerPromiseCB(initWorkerPromise, pluginName, worker);
			}
		);
	} else {
		const worker = new Worker(
			(debug ? '/public' : prodConf.publicPath) +
				'/plugins/' +
				pluginName +
				'/index.js'
		);

		initWorkerPromiseCB(initWorkerPromise, pluginName, worker);
	}
};

export const getDepCode = (
	pluginName,
	promise,
	worker,
	depPath,
	depType,
	store
) => {
	if (!debug) {
		page.cmd(
			'fileGet',
			['plugins/' + pluginName + '/' + depPath, 'text'],
			f => {
				const depCode =
					'data:text/javascript;base64;charset=utf8,' +
					btoa(unescape(encodeURIComponent(f)));

				if (depType === 'css') {
					checkloadjscssfile(
						(debug ? '/public' : prodConf.publicPath) +
							'/plugins/' +
							pluginName +
							'/' +
							depPath,
						depType
					);
				}

				worker.postMessage({
					depPath,
					depType,
					depCode
				});
			}
		);
	} else {
		const depCode = depPath;

		if (depType === 'css') {
			checkloadjscssfile(
				(debug ? '/public' : prodConf.publicPath) +
					'/plugins/' +
					pluginName +
					'/' +
					depPath,
				depType
			);
		}

		worker.postMessage({
			depPath,
			depType,
			depCode
		});
	}
};

export default {
	installPlugin,
	getDepCode
};
